"use client"

import { useState } from "react"
import { Search, Eye, X, ChevronDown } from "lucide-react"
import { mockOrders, type OrderStatus, orderStatusLabels, orderStatusColors } from "@/lib/mock-data"

type Order = (typeof mockOrders)[0]

const statusFlow: OrderStatus[] = ["aprovado", "em_producao", "a_caminho", "entregue"]

export default function AdminPedidos() {
  const [orders, setOrders] = useState(mockOrders)
  const [searchTerm, setSearchTerm] = useState("")
  const [filterStatus, setFilterStatus] = useState<string>("")
  const [selectedOrder, setSelectedOrder] = useState<Order | null>(null)
  const [showStatusDropdown, setShowStatusDropdown] = useState<string | null>(null)

  const filteredOrders = orders.filter((o) => {
    const matchSearch =
      o.id.toLowerCase().includes(searchTerm.toLowerCase()) ||
      o.clienteNome.toLowerCase().includes(searchTerm.toLowerCase())
    const matchStatus = !filterStatus || o.status === filterStatus
    return matchSearch && matchStatus
  })

  const updateOrderStatus = (orderId: string, newStatus: OrderStatus) => {
    setOrders(
      orders.map((o) =>
        o.id === orderId ? { ...o, status: newStatus, updatedAt: new Date().toISOString().split("T")[0] } : o,
      ),
    )
    setShowStatusDropdown(null)
    if (selectedOrder?.id === orderId) {
      setSelectedOrder({ ...selectedOrder, status: newStatus })
    }
  }

  return (
    <div className="p-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-light text-foreground">Pedidos</h1>
        <p className="text-foreground/60">Gerencie e acompanhe os pedidos dos clientes</p>
      </div>

      {/* Filters */}
      <div className="flex flex-col sm:flex-row gap-4 mb-6">
        <div className="relative flex-1">
          <Search size={18} className="absolute left-3 top-1/2 -translate-y-1/2 text-foreground/50" />
          <input
            type="text"
            placeholder="Buscar por ID ou cliente..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="w-full pl-10 pr-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
          />
        </div>
        <select
          value={filterStatus}
          onChange={(e) => setFilterStatus(e.target.value)}
          className="px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
        >
          <option value="">Todos os status</option>
          {Object.entries(orderStatusLabels).map(([value, label]) => (
            <option key={value} value={value}>
              {label}
            </option>
          ))}
        </select>
      </div>

      {/* Status Summary */}
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-6 gap-4 mb-6">
        {Object.entries(orderStatusLabels).map(([status, label]) => {
          const count = orders.filter((o) => o.status === status).length
          return (
            <button
              key={status}
              onClick={() => setFilterStatus(filterStatus === status ? "" : status)}
              className={`p-4 rounded-xl border transition-all ${
                filterStatus === status ? "border-accent bg-accent/5" : "border-border bg-card hover:border-accent/50"
              }`}
            >
              <p className="text-2xl font-semibold text-foreground">{count}</p>
              <p className="text-sm text-foreground/60">{label}</p>
            </button>
          )
        })}
      </div>

      {/* Table */}
      <div className="bg-card border border-border rounded-xl overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-muted border-b border-border">
              <tr>
                <th className="px-6 py-4 text-left text-sm font-medium text-foreground">Pedido</th>
                <th className="px-6 py-4 text-left text-sm font-medium text-foreground">Cliente</th>
                <th className="px-6 py-4 text-left text-sm font-medium text-foreground">Total</th>
                <th className="px-6 py-4 text-left text-sm font-medium text-foreground">Data</th>
                <th className="px-6 py-4 text-left text-sm font-medium text-foreground">Status</th>
                <th className="px-6 py-4 text-center text-sm font-medium text-foreground">Ações</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-border">
              {filteredOrders.map((order) => (
                <tr key={order.id} className="hover:bg-muted/50 transition-colors">
                  <td className="px-6 py-4 font-medium text-foreground">{order.id}</td>
                  <td className="px-6 py-4">
                    <div>
                      <p className="text-foreground">{order.clienteNome}</p>
                      <p className="text-sm text-foreground/60">{order.clienteEmail}</p>
                    </div>
                  </td>
                  <td className="px-6 py-4 font-medium text-foreground">
                    R$ {order.total.toLocaleString("pt-BR", { minimumFractionDigits: 2 })}
                  </td>
                  <td className="px-6 py-4 text-sm text-foreground/70">{order.createdAt}</td>
                  <td className="px-6 py-4">
                    <div className="relative">
                      <button
                        onClick={() => setShowStatusDropdown(showStatusDropdown === order.id ? null : order.id)}
                        className={`flex items-center gap-1 px-3 py-1 rounded-full text-xs font-medium ${orderStatusColors[order.status]}`}
                      >
                        {orderStatusLabels[order.status]}
                        <ChevronDown size={14} />
                      </button>

                      {showStatusDropdown === order.id && (
                        <div className="absolute top-full left-0 mt-1 bg-card border border-border rounded-lg shadow-lg z-10 min-w-[160px]">
                          {Object.entries(orderStatusLabels).map(([status, label]) => (
                            <button
                              key={status}
                              onClick={() => updateOrderStatus(order.id, status as OrderStatus)}
                              className={`w-full px-4 py-2 text-left text-sm hover:bg-muted transition-colors first:rounded-t-lg last:rounded-b-lg ${
                                order.status === status ? "bg-muted font-medium" : ""
                              }`}
                            >
                              <span
                                className={`inline-block w-2 h-2 rounded-full mr-2 ${orderStatusColors[status as OrderStatus].replace("text-", "bg-").split(" ")[0]}`}
                              ></span>
                              {label}
                            </button>
                          ))}
                        </div>
                      )}
                    </div>
                  </td>
                  <td className="px-6 py-4">
                    <div className="flex items-center justify-center">
                      <button
                        onClick={() => setSelectedOrder(order)}
                        className="p-2 text-foreground/60 hover:text-primary hover:bg-muted rounded-lg transition-colors"
                      >
                        <Eye size={18} />
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>

      {/* Order Detail Modal */}
      {selectedOrder && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-card border border-border rounded-xl w-full max-w-2xl max-h-[90vh] overflow-y-auto">
            <div className="flex items-center justify-between p-6 border-b border-border">
              <div>
                <h2 className="text-xl font-medium text-foreground">Pedido {selectedOrder.id}</h2>
                <p className="text-sm text-foreground/60">Criado em {selectedOrder.createdAt}</p>
              </div>
              <button onClick={() => setSelectedOrder(null)} className="text-foreground/60 hover:text-foreground">
                <X size={20} />
              </button>
            </div>

            <div className="p-6 space-y-6">
              {/* Status */}
              <div className="flex items-center justify-between">
                <span className="text-sm font-medium text-foreground">Status do Pedido</span>
                <span
                  className={`px-3 py-1 rounded-full text-sm font-medium ${orderStatusColors[selectedOrder.status]}`}
                >
                  {orderStatusLabels[selectedOrder.status]}
                </span>
              </div>

              {/* Status Flow */}
              <div className="flex items-center justify-between">
                {statusFlow.map((status, idx) => {
                  const isActive = statusFlow.indexOf(selectedOrder.status as OrderStatus) >= idx
                  const isCurrent = selectedOrder.status === status
                  return (
                    <div key={status} className="flex items-center">
                      <div className={`flex flex-col items-center ${idx > 0 ? "ml-2" : ""}`}>
                        <div
                          className={`w-8 h-8 rounded-full flex items-center justify-center text-xs font-medium ${
                            isActive ? "bg-accent text-white" : "bg-muted text-foreground/50"
                          } ${isCurrent ? "ring-2 ring-accent ring-offset-2" : ""}`}
                        >
                          {idx + 1}
                        </div>
                        <span className="text-xs text-foreground/60 mt-1">{orderStatusLabels[status]}</span>
                      </div>
                      {idx < statusFlow.length - 1 && (
                        <div className={`w-12 h-0.5 mx-2 ${isActive ? "bg-accent" : "bg-muted"}`} />
                      )}
                    </div>
                  )
                })}
              </div>

              {/* Cliente Info */}
              <div className="bg-muted rounded-lg p-4">
                <h3 className="font-medium text-foreground mb-3">Dados do Cliente</h3>
                <div className="grid grid-cols-2 gap-4 text-sm">
                  <div>
                    <p className="text-foreground/60">Nome</p>
                    <p className="text-foreground">{selectedOrder.clienteNome}</p>
                  </div>
                  <div>
                    <p className="text-foreground/60">E-mail</p>
                    <p className="text-foreground">{selectedOrder.clienteEmail}</p>
                  </div>
                  <div>
                    <p className="text-foreground/60">Celular</p>
                    <p className="text-foreground">{selectedOrder.clienteCelular}</p>
                  </div>
                  <div>
                    <p className="text-foreground/60">Pagamento</p>
                    <p className="text-foreground">{selectedOrder.pagamento}</p>
                  </div>
                </div>
              </div>

              {/* Endereço */}
              <div className="bg-muted rounded-lg p-4">
                <h3 className="font-medium text-foreground mb-3">Endereço de Entrega</h3>
                <p className="text-sm text-foreground">
                  {selectedOrder.endereco.rua}
                  <br />
                  {selectedOrder.endereco.bairro}
                  <br />
                  {selectedOrder.endereco.cidade} - {selectedOrder.endereco.estado}
                  <br />
                  CEP: {selectedOrder.endereco.cep}
                </p>
              </div>

              {/* Items */}
              <div>
                <h3 className="font-medium text-foreground mb-3">Itens do Pedido</h3>
                <div className="space-y-3">
                  {selectedOrder.items.map((item, idx) => (
                    <div
                      key={idx}
                      className="flex items-center justify-between py-2 border-b border-border last:border-0"
                    >
                      <div>
                        <p className="text-foreground">{item.produtoNome}</p>
                        <p className="text-sm text-foreground/60">Qtd: {item.quantidade}</p>
                      </div>
                      <p className="font-medium text-foreground">
                        R${" "}
                        {(item.precoUnitario * item.quantidade).toLocaleString("pt-BR", { minimumFractionDigits: 2 })}
                      </p>
                    </div>
                  ))}
                </div>

                {/* Totals */}
                <div className="mt-4 pt-4 border-t border-border space-y-2">
                  <div className="flex justify-between text-sm">
                    <span className="text-foreground/60">Subtotal</span>
                    <span className="text-foreground">
                      R$ {selectedOrder.subtotal.toLocaleString("pt-BR", { minimumFractionDigits: 2 })}
                    </span>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span className="text-foreground/60">Frete</span>
                    <span className="text-foreground">
                      {selectedOrder.frete === 0
                        ? "Grátis"
                        : `R$ ${selectedOrder.frete.toLocaleString("pt-BR", { minimumFractionDigits: 2 })}`}
                    </span>
                  </div>
                  <div className="flex justify-between font-medium text-lg pt-2">
                    <span className="text-foreground">Total</span>
                    <span className="text-foreground">
                      R$ {selectedOrder.total.toLocaleString("pt-BR", { minimumFractionDigits: 2 })}
                    </span>
                  </div>
                </div>
              </div>
            </div>

            <div className="flex items-center justify-end gap-3 p-6 border-t border-border">
              <button
                onClick={() => setSelectedOrder(null)}
                className="px-4 py-2 border border-border rounded-lg hover:bg-muted transition-colors"
              >
                Fechar
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
